.sidebarChannel {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.sidebarChannel > .delete {
  margin-right: 20px;
  display: none;
}

.sidebarChannel:hover > .delete {
  display: block;
  cursor: pointer;
}


.sidebarChannel:hover {
    background-color: rgb(210, 241, 255);
  }

.sidebarChannel.active {
  background-color:  rgb(210, 241, 255);
  width: 100%;
}
  
  .sidebarChannel > h4 {
    display: flex;
    margin-left: 20px;
    color: black;
    cursor: pointer;
  }