
.sidebar {
    display: flex;
    flex-direction: column;
    flex: 0.2;
    height: 100vh;
    background-color: white;
    border: rgb(121, 161, 249);
  }
  
  .sidebar__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px;
    margin-left: 20px;
    background-color: white;
    color: black;
  }
  
  .sidebar__channels {
    flex: 1;
  }
  
  .sidebar__addChannel {
    cursor: pointer;
  }
  
  .sidebar__addChannel:hover {
    color: white;
  }
  
  .sidebar__channelsHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: black;
    margin-right: 20px;
    margin-left: 20px;
  }
  
  .sidebar__header {
    display: flex;
    align-items: center;
  }
  
  .sidebar__profile {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: black;
    background-color: white;
    margin-left: 20px;
  }
  
  .sidebar__profileInfo {
    flex: 1;
  }
  
  .sidebar__profileInfo > h3 {
    color: black;
  }

  .sidebar__profileInfo > img {
    width: 50px;
    height: auto;
    border-radius: 50px;
  }

  .sign-out {
    cursor: pointer;
  }

  .sign-out:hover {
    text-decoration: underline;
  }