.message {
  display: flex;
  flex-direction: column;
  padding: 5px;
  color: black;
  }
  
  .message__info {
    margin-left: 20px;
    display: flex;
    align-items: center;
  }
  
  .message__timestamp {
    color: gray;
    margin-left: 20px;
    font-size: x-small;
  }

  .message__info > h4 {
    margin-left: 20px;
  }

  .message__info > img {
    width: 40px;
    height: 40px;
    border-radius: 50px;
  }

  .message__message {
    margin-left: 50px;
  }