.login {
    display: grid;
    place-items: center;
    height: 100vh;
    width: 100%;
  }
  
  .login > button {
    width: 150px;
    height: 50px;
    background-color: blue;
    color: white;
    border: none;
    border-radius: 50px;
  }
  
  .login > button:hover {
    color: #7289da;
  }
  
  .login > div > img {
    object-fit: contain;
    height: 150px;
  }

