.chat {
    display: flex;
    flex-direction: column;
    flex: 0.8;
    background-color: rgb(210, 241, 255);
    height: 100vh;
    overflow-y: scroll;
    border: 1px solid rgb(121, 161, 249);
  }
  
  .chat__messages {
    flex: 1;
    overflow: scroll;
  }
  
  .chat__messages::-webkit-scrollbar {
    display: none;
  }
  
  .chat__messages {
    -ms-overflow-style: none;
    scrollbar-width: none;
  }
  
  .chat__input {
    color: black;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px;
    border-radius: 5px;
    margin: 20px;
    border: 1px solid gray;
  
  }
  
  .chat__input > form > input {
    padding: 15px;
    background: transparent;
    border: none;
    width: 100%;
    outline-width: 0;
    color: black;
    font-size: large;
  }
  
  .chat__input > form {
    flex: 1;
  }
  
  .chat__inputButton {
    display: none;
  }
  
  .chat__inputIcons {
    padding: 5px;
  }